body {
  background-color: #f5f5f1;
}
.container {
  *zoom: 1;
  max-width: 75%;
  margin-left: auto;
  margin-right: auto;
}
.container:before {
  content: '';
  display: table;
}
.container:after {
  content: '';
  display: table;
  clear: both;
}
.shows {
  width: 100%;
  height: calc(99.99% * 1/1 - (30px - 30px * 1/1));
  margin-bottom: 30px;
}
.shows:last-child {
  margin-bottom: 0;
}
.show {
  width: calc(99.99% * 1/5 - (30px - 30px * 1/5));
  height: calc(99.99% * 1/5 - (30px - 30px * 1/5));
  position: relative;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  color: inherit;
  text-decoration: none;
}
.show:nth-child(1n) {
  float: left;
  margin-right: 30px;
  margin-bottom: 30px;
  clear: none;
}
.show:last-child {
  margin-right: 0;
  margin-bottom: 0;
}
.show:nth-child(5n) {
  margin-right: 0;
  float: right;
}
.show:nth-child(5n + 1) {
  clear: left;
}
.show:nth-last-child(-n + 5) {
  margin-bottom: 0;
}
.show:hover {
  background-color: #e3e3e3;
}
.show a {
  color: inherit;
  text-decoration: none;
}
.show a:hover {
  text-decoration: underline;
}
.button {
  display: inline-block;
  text-align: center;
  line-height: 1;
  cursor: pointer;
  -webkit-appearance: none;
  transition: background-color 0.25s ease-out, color 0.25s ease-out;
  vertical-align: middle;
  border: 1px solid transparent;
  border-radius: 0;
  padding: 0.85em 1em;
  margin: 0 0 1rem 0;
  font-size: 0.9rem;
  background-color: #2199e8;
  color: #fefefe;
  text-decoration: none;
}
.button:hover {
  background-color: #4daded;
}
.left {
  float: left;
}
.right {
  float: right;
}
